.aside {
    width: 201px;
    height: 100%;
    background: #3b444b;
    mix-blend-mode: normal;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 80px;
}

.avatarContainer {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background: white;
    margin: 0px 0px 20px 0px;
}

.name {
    font-size: 1.3em;
    margin: 10px;
}

.uId {
    font-size: 1em;
    margin: 5px;
    
    padding: 5px 10px;
    border-radius: 8px;
    background: rgba(black,0.2);
} 

.idPre {
    display: inline-block;
    opacity: 0.5;
    margin-right: 4px;
}

