.nav {
    width: 261.5px;
    height: 100%;
    background: #37474f;
    mix-blend-mode: normal;
    
    padding: 35.5px 24px 35.5px 26px;
    
}

.navhead {
    min-height: 71px;
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: auto auto;
    
}

.settingsLink {
    width: 14px;
    height: 15px;
    justify-self: end;
    align-self: center;
    
    display: block;
    transition: 0.4s ease-in-out;
}

.settingsIc {
   
    
    transition: 0.4s ease-in-out;
}

@media (hover: hover) and (pointer: fine) {
.settingsIc:hover path  {
        opacity: 1;
    }
}

    .listNomCont {
        display: flex;
        align-items: center;
        
    }

.listNom {
    font-size: 1.428em;
    color: #FFFFFF;
    font-weight: 700;
    
}





.allThreads {
    font-size: 1em;
    color: #B5B5B5; 
    align-self: flex-end;

}

.allThreads::before {
    
        content: '';
        display: inline-block;
        background: url(img/allBefore.svg);
        width: 16px;    
        height: 17px;
        position: relative;
        top: 4px;
        margin-right: 11px;
    
}

.friendsList {
    padding-top: 13px;
}

.friend {
 
    display: flex;
    align-items: center;
    margin: 7px 0px 7px -6.5px;
    width: 100%;
    padding: 5px;
    cursor: pointer;
    border-radius: 5px;
}

    .friend.isChosen {
        background: rgba(black, 0.2);
    }


.onlineStatus {
    width: 8px;
    height: 7px;
    border-radius: 50%;
    background: #666666;
    
    
}

.avatar {
    height: 25px;
    width: 25px;
    background: grey;
    border-radius: 5px;
    margin-left: 8px;
    
 
}

.friendsName {
    font-size: 1em;
    /* identical to box height */
    color: #B5B5B5;
    margin-left: 8px;

}
