@keyframes friendAnim {
    0% {
        left: -100px;
        opacity: 0;
        
    }

    100% {
        left: 10px;
        opacity: 1;
    }
}




.modalWindow {
    bottom: 90px;
    left: 10px;
    position: absolute;
    overflow: hidden;
    animation-name: friendAnim;
    width: 300px;
    height: 100px;
    background: #3b444b;
    border: 1.5px solid gray;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    animation-duration: 0.3s;
    animation-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
    animation-fill-mode: both;
    animation-iteration-count: 1;


}



.friendIDcont {
    width: 240px;
    border-radius: 8px;
    position: relative;
    background: #00AB78;
    margin-top: 6px;
    transition: 0.2s cubic-bezier(0.23, 1, 0.320, 1);
    
}


.friendIDcont::before {
    content: "Enter your friend's ID";
    position: absolute;
    font-size: 0.8em;
    font-weight: 200;
    padding-left: 3px;
    width: 240px;
    pointer-events: none;
    top: -16px;
    color: white;
}

.friendID {
    vertical-align: top;
    width: 200px;
    border: none;
    border-radius: 8px;
    height: 35px;
    font-size: 1.1em;
    font-weight: 400;
    padding: 0px 15px 0px 30px;
    color: #3b444b;
    caret-color: #3b444b;
    background: white;
    -moz-appearance:textfield;
}

.friendID::-webkit-outer-spin-button,
.friendID::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

.hashTag {
    color: #4a555e;
    width: 20px;
    font-size: 1.5em;
    height: fit-content;
    left: 8px;
    position: absolute;
    text-align: center;
    top: 6.5px;
}

.friendID:focus {
    border: none;
    outline: none;
}

.addFriendButton {
    display: inline-flex;
    flex-direction: column;
    vertical-align: top;
    justify-content: center;
    align-items: center;
    width: 35px;
    cursor: pointer;
    margin-left: 2px;
    height: 100%;
    position: relative;
    transition: 0.3s ease-in-out;
}

.addFriendButton.loading {
    animation-name: loading;
    animation-duration: 0.5s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}


@keyframes loading {
    0% {
        transform: rotateZ(0deg);
        
    }

    100% {
        transform: rotateZ(360deg);
    }
}

.addFriendButton::before {
    content: '';
    transform: rotateZ(90deg);
    display: block;
    width: 50%;
    background: white;
    height: 4px;
    border-radius: 8px;
    position: absolute;
    
}

.addFriendButton::after {
    content: '';
    display: block;
    width: 50%;
    background: white;
    height: 4px;
    border-radius: 8px;
    position: absolute;
    cursor: pointer;
}


// animations

.modalChatWindow {
    position: absolute;
    top: calc(50% - 200px);
    left: calc(50% - 150px);
    width: 300px;
    background: #3b444b;
    height: 400px;
    padding: 25px 25px;
    border-radius: 15px;
    overflow: auto;
    display: flex;
    flex-direction: column;
}

.friendAvatar {
    height: 25px;
    width: 25px;
    background: grey;
    border-radius: 5px;
    margin-right: 8px;
    display: inline-block;
    vertical-align: middle;
}

.friendSearchCont {
    background: white;

    display: flex;
    border-radius: 5px;
    overflow: hidden;
    align-items: center;
    flex-shrink: 0;
}

.searchIco {
    width: 30px;
    flex-grow: 0;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 2px solid #37474f;
    height: 100%;
}

.add-Btn {
    height: 25px;
    width: 25px;
    position: absolute;
    right: 10px;
    border-radius: 50%;
    background: #9EDEC6;
    vertical-align: middle;
    outline: none;
    cursor: pointer;
    border: none;
    top: 5px;
    padding: 0px;
}

.add-Btn::before {
    content: '';
    display: block;
    width: 40%;
    height: 2px;
    background: #3b444b;
    transform: rotateZ(90deg);
    margin-top: 0px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
}

.add-Btn::after {
    content: '';
    display: block;
    width: 40%;
    transform: rotateZ(0deg);
    height: 2px;
    background: #3b444b;
    margin-top: -2px;
    border-radius: 10px;
    margin-left: auto;
    margin-right: auto;
}

.friendSearch {
    display: block;
    font-size: 1em;
    line-height: 100%;
    min-height: 30px;
    background: white;
    padding: 0px 10px;
    flex-grow: 1;
    color: #37474f;
    letter-spacing: 0.5px;
    width: auto;
    
    outline: none;
    border: 2px solid mintcream;
    
}

.friendSearch:focus {
    
    outline: none;
}


.availableFriendItem {
    list-style-type: none;
    padding: 0px 35px 0px 10px;
    position: relative;
    height: 35px;
    background: #37474f;
    color: white;
    margin: 5px 12px;
    line-height:32px;
    border-radius: 5px;
   
}

.nameFr {
    vertical-align: middle;
    white-space: nowrap;
    display: inline-block;
    overflow: auto;
    overflow: overlay;
    max-width: 120px;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.nameFr::-webkit-scrollbar {
    display: none;
  }
  
  /* Hide scrollbar for IE, Edge and Firefox */
  .nameFr {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }

.availableFriendsCont {
    flex-grow: 1;
    overflow: auto;
    background: white;
    width: auto;
    border-radius: 5px;
    margin: 25px 0px 0px;
    padding: 5px 0px;
}

/* ===== Scrollbar CSS ===== */
  /* Firefox */
  .availableFriendsCont {
    scrollbar-width: auto;
    scrollbar-color: #37474f transparent;
  }

  /* Chrome, Edge, and Safari */
  .availableFriendsCont::-webkit-scrollbar {
    width: 16px;
  }

  .availableFriendsCont::-webkit-scrollbar-track {
    background: transparent;
  }

  .availableFriendsCont::-webkit-scrollbar-thumb {
    background-color: #37474f;
    border-radius: 10px;
    border: 3px solid #ffffff;
  }



